@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&family=Raleway:wght@100;200&display=swap");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(10, 10, 1);
}
p{
text-align: justify;
margin: 2em 10em;
color: #002734;
line-height: 35px;
}
/* Header Section */
header {
  width: 100%;
  background-color: rgba(23, 23, 23, 0.15);
  padding: 10px;
  position: absolute;
  top: 0;
}

header .container {
  display: flex;
  justify-content: space-between;
  width: 1180px;
  margin: auto;
}

/* Header Section  end*/

.banner-container {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(5, 60, 87, 0.849), rgb(3, 96, 133)),
    url(./background.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  display: flex;
  align-items: center;
}

.banner-container .container {
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
}

.left-section h1 {
  margin: 0;
}

.left-section {
  width: 600px;
  text-align: center;
}

.left-section h1 {
  margin: 0;
  font-size: 40px;
  color: white;
}

.left-section p {
  margin: 0;
  font-size: 14px;
  letter-spacing: 1px;
  color: rgba(255, 255, 255, 0.884);
  margin-top: 20px;
  line-height: 18px;
  text-align: center;
}

.left-section button {
  color: #008b9b;
  border: none;
  padding: 15px 55px;
  margin-top: 15px;
  border-radius: 3px;
  cursor: pointer;
 transition: .5s;
}

footer {
  text-align: center;
  color: gray;
  margin-bottom: 10px;
}
footer span {
  color: #057698;
  font-weight: bold;
}

.card {
  display: flex;
  justify-content: space-evenly;
  font-family: "Raleway", sans-serif;
  text-align: center;
  margin-bottom: 1em;
  align-items: center;
 
}


@media screen and (max-width:650px) { 
  .card{

    flex-direction: column;
  }
  
}

.card-item {
  background-color: rgb(255, 255, 255);

  height: 15em;
  margin: 2px 5px;
  width: 300px;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 2px;
  transition: .3s;
}

.card-item:hover {
  padding: 5px;
}

h4,
h2 {
  text-align: center;
  font-family: "Poppins", sans-serif;
  color: #003c4e;

}

.card img {
  width: 100%;
  height: 65%;
}

.site-footer {
  width: 100%;
  height: 200px;
  background-color: rgb(255, 255, 255);

  left: 0;
  bottom: 0;
  width: 100%;

  color: rgb(0, 0, 0);
  text-align: center;
}
.btn:hover{
  background-color: transparent;
  border:1px solid rgb(255, 255, 255);
  color: white;
  

}